* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.project-header {
  overflow: hidden;
  align-items: center;
  width: 100%;
  height: 300px;
  /* text-align: center;
  justify-items: center; */
  margin: 0;
}
.project-header .project-title {
  margin: 0;
  position: relative;
  top: 20%;
  left: 5%;
  text-transform: uppercase;
  font-size: 35px;
  text-transform: uppercase;

  background: rgb(22, 8, 252);
  background: linear-gradient(
    90deg,
    rgba(22, 8, 252, 1) 0%,
    rgba(241, 19, 255, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  vertical-align: middle;
}
.project-description {
  position: relative;
  top: 25%;
  left: 5%;
  width: 50%;
  font-weight: 100;
  color: black;
  margin: 0;
}

.pSection {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100vh;
  display: flex;
}
.pSection .slide {
  position: relative;
  background: whitesmoke;
  height: 100%;
  flex: 1 1;

  overflow: hidden;
  transition: 0.5s ease-in-out;
}
.pSection .slide:last-child {
  border-right: none;
}
.pSection .slide:hover {
  flex-grow: 5;
}
.pSection .slide:nth-child(1) {
  background: url(/portofolio/static/media/food-min.2a6567bb.jpg);
  background-size: contain;
}
.pSection .slide:nth-child(2) {
  background: url(/portofolio/static/media/fashion-min.fcad919f.jpg);
  background-size: contain;
}
.pSection .slide:nth-child(3) {
  background: url(/portofolio/static/media/edit-min.c9f7d6a4.jpg) 50% 50%;
  background-size: cover;
}
.pSection .slide:nth-child(4) {
  background: url(/portofolio/static/media/video-min.f67f8878.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
.pSection .slide:nth-child(5) {
  background: url(/portofolio/static/media/fb.7a168da5.jpg) 50% 50%;
  background-size: cover;
}
.pSection .slide .content {
  position: absolute;
  bottom: 0;
  margin: 40px;
  padding: 30px;
  background: #000;
  color: #000;
  visibility: hidden;
  opacity: 0;
  transition: 0s;
  transform: translateY(100px);
}
.pSection .slide:hover .content {
  visibility: visible;
  transition: 0.7s;
  transition-delay: 0.5s;
  transform: translateY(0);
  opacity: 0.9;
}
.content-text {
  font-size: 85%;
  color: white;
  opacity: 1;
}

