  height: 50px;
  color: rgba(0, 212, 255, 1);
  border: 1px solid rgba(0, 212, 255, 1);
  border-radius: 10px;
  font-size: 20px;
  transition: 0.5s;
  overflow: hidden;
}

.project-CTA-button button:focus {
  outline: none;
}

.project-CTA-button button::before {
  content: "Gabung";
  display: block;
  position: absolute;
  background: rgba(22, 8, 252, 0.1);
  width: 60px;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0.5s;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  transform: translate(-130px) skewX(15deg);
}
.project-CTA-button button::after {
  content: "Gabung";
  display: block;
  position: absolute;
  background: rgba(22, 8, 252, 0.2);
  width: 30px;
  height: 100%;
  top: 0;
  left: 30px;
  opacity: 0;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  transform: translate(-100px) skewX(10deg);
}
.project-CTA-button button:hover {
  border-color: rgba(22, 8, 252, 1);
  background: rgb(22, 8, 252);
  background: linear-gradient(
    90deg,
    rgba(22, 8, 252, 1) 0%,
    rgba(241, 19, 255, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
  color: white;
}
.project-CTA-button button:hover:before {
  transform: translate(300px) skewX(-15deg);
  opacity: 0.6;
  transition: 0.7s;
  color: white;
}
.project-CTA-button button:hover:after {
  transform: translate(300px) skewX(-15deg);
  opacity: 1;
  transition: 0.7s;
  color: white;
}

